.followBtn button {
  padding: 5px 15px;
  background-color: #5181b8;
  color: #fff;
  font-size: 13px;
  border-radius: 4px;
}

.followed button {
  background: #e5ebf1;
  color: #55677d;
}

.disabled button {
  cursor: default;
}

.big button {
  padding: 10px 30px;
  font-size: 16px;
}
